/*container principal*/
.searchResults {
    box-sizing: border-box;
    background-color: rgb(14, 13, 13);
    opacity: 0.95;
    color: whitesmoke;
    width: 85%;
    padding: 1rem 1.5rem;
    margin: 0 auto;
}

.searchBanner {
    display: flex;
    justify-content: space-between;
}

.titulo {
    margin-left: 1.5rem;
}

.buttonDiv p {
    margin: 0.2em 0 0 0;
    font-size: 0.75em;
}

.boton {
    width: 3.5em;
    height: 3.5em;
    border-radius: 50%;
    background-color: rgb(69, 6, 6);
    color: whitesmoke;
}
.boton:hover{
    cursor: pointer;
}

.remolino {
    font-size: 1.5em;
}

.lineaHorizontal {
    background-color: whitesmoke;
    width: 80%;
    height: 2px;
    margin: 1rem auto;
}

.resultList {
    display: flex;
    justify-content: center;
}

.playlistTitle{
    color: whitesmoke;
    background-color: transparent;
    border: none;
    display: block;
    width: 12.5rem;
    font-size: 1.5em;
    font-weight: 400;
    margin-block-start: 0.83em;
    margin-block-end: 0.83em;
    margin-left: 1.5rem;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    border-radius: 10px;
}

.x{
    background-color: rgb(29, 185, 84);
    color: white;
    height: 3.2rem;
    border: 1px solid white;
    font-size: 1.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    margin: 1rem auto;
    padding: 1rem;
}

.x:hover{
    cursor: pointer;
}

.x:active{
    background-color: rgb(17, 152, 42);
    position: relative;
    top: 2px;
    left: 2px;
}